@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700;900&display=swap);
* {
  box-sizing: border-box;
}

#app {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #e5d9f1;
  min-height: 100vh;
}

body {
  padding: 0;
}

#app {
  margin: 2.5rem auto;
  padding: 1rem;
  width: 100%;
  position: absolute;
  border-radius: 6px;
}

.typo-animation-container {
  font-family: Arial, sans-serif;
  padding: 1rem;
  border-radius: 8px;
  background-color: #f9f9f9;
  max-width: 600px;
  margin: 10px auto;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.percent-container {
  font-size: 1em;
  font-weight: bold;
  color: #4caf50;
  margin-bottom: 10px;
}

.percent-label {
  font-weight: normal;
  color: #333;
}

.percent-value {
  color: #4caf50;
  margin-left: 5px;
}

.keywords-container {
  margin-top: 15px;
}

.keywords-label {
  font-size: .9em;
  font-weight: bold;
  color: #e74c3c;
}

.keywords-list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
}

.keyword-item {
  background-color: #ffe6e6;
  color: #e74c3c;
  padding: 5px 10px;
  border-radius: 5px;
  margin: 5px;
  font-size: 0.9em;
}

.text-container {
  margin-top: 15px;
  font-size: 1.2em;
  line-height: 1.5;
  color: #333;
}

.final-thoughts-label {
  font-weight: bold;
  font-size: .9em;
  margin-right: 5px;
}

.displayed-text {
  display: inline;
  white-space: pre;
}

.cursor {
  display: inline;
  animation: blink 0.8s steps(2, start) infinite;
  color: #4caf50;
  font-weight: bold;
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
.title {
  text-transform: uppercase;
  font-size: 20px;
  font-family: 'Josefin Sans, sans-serif';
  cursor: pointer;
  margin: 10px;
}

.header {
  flex-direction: row;
  justify-content: space-between;
}

.divider {
  margin: 10px;
  width: 100%;
  background-color: grey;
}

.appMenu {
  width: "100%";
}

.navList {
  width: 300;
}

.menuItem {
  width: 300;
}

.menuItemIcon {
  color: "#97c05c";
}

.title {
  margin-right: "auto";
}

.drawer {
  width: 300;
}

.content {
  padding: 30;
}

.header {
  background-color: "#0d6efd";
  padding-right: "79px";
  padding-left: "118px";
}

.logo {
  font-family: "Work Sans; sans-serif";
  font-weight: 600;
  color: "#FFFEFE";
  text-align: "left";
}

.logo:hover {
  color: "#000000";
}


.menuButton:hover {
  background-color: "#1E90FF";
  color: "#000000";

}

.toolbar {
  display: "flex";
  justify-content: "space-between";
}
.main {
  height: 100%;
  width: 100%;
  background-color: rgb(21, 21, 21);
}

.Auth-form-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 10px;
  margin-right: 5%;
}

.Auth-form {
  min-width: 28em;
  /* height: 35em; */
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.funnelChart {
  clip-path: polygon(0 0, 100% 0, 60% 78%, 60% 90%, 40% 100%, 40% 78%);
  text-align: center;
}

.funnelChart .chartSegment {
  width: 100%;
  min-height: 1px;
  font-size: 12px;
  padding: 10px 0;
  margin: 2px 0;
  color: black;
  background-color: yellow;
}

.funnelChart .chartSegment:last-child {
  padding-bottom: 40px;
}

.funnelChart .chartSegment:hover {
  opacity: 0.7;
}

.funnelChart .chartSegment .segmentTitle {
  font-size: 1.1rem;
  padding-bottom: 5px;
}
